// @flow
import * as React from 'react';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { Box, Grid } from '@material-ui/core';
import { formatPrice } from 'helpers/number';

const useStyles = makeStyles(theme => ({
  root: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
    marginBottom: theme.spacing(3),
    [theme.breakpoints.up('sm')]: {
      minHeight: '180px',
    },
  },
  attributesWrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
  contentWrapper: {
    padding: '26px 24px 24px',
  },
  activeWrapper: {
    padding: '19px 24px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    backgroundColor: theme.palette.background.lightGray,
    borderTop: '1px solid #E0E0E0',
  },
  propertyName: {
    marginBottom: theme.spacing(1.5),
  },
  media: {
    backgroundSize: 'cover',
    [theme.breakpoints.down('xs')]: {
      paddingTop: '80%',
    },
    [theme.breakpoints.up('sm')]: {
      height: '100%',
    },
  },
  price: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '1.25rem',
  },
}));

type Props = {
  property: Object,
};

const Property = ({ property }: Props): React.Element<typeof Grid> => {
  const image = _get(property, 'Thumbnail') || '/img/placeholder.png';
  const listPrice = _get(property, 'ListingDetails.ListPrice', '');
  const fullAddress = _get(property, 'PropertyData.FullAddress', '');
  const classes = useStyles();

  return (
    <Grid container className={classes.root}>
      <Grid item md={3} sm={4} xs={12}>
        <CardMedia className={classes.media} image={image} title="avatar" />
      </Grid>
      <Grid item md={9} sm={8} xs={12} className={classes.attributesWrapper}>
        <Box component="div" className={classes.contentWrapper}>
          <Typography
            variant="h3"
            color="textPrimary"
            component="p"
            className={classes.propertyName}
          >
            {fullAddress}
          </Typography>
        </Box>
        <Box component="div" className={classes.activeWrapper}>
          <Typography variant="h3" className={classes.price} component="span">
            {listPrice ? formatPrice(listPrice) : 'TBD'}
          </Typography>
        </Box>
      </Grid>
    </Grid>
  );
};

export default React.memo(Property);
